nav {
  width: 100%;
  position: fixed;
  top: 0;
  height: 50px;
  background-color: darkgreen;
  display: flex;
  align-items: center;
}

.nav-list {
  width: 100%;
  /* height: 100%; */
  background-color: darkgreen;
  display: flex;
  align-items: center;
  justify-content: center;
  list-style-type: none;
  font-size: 22px;
}

.items a{
  margin: 0 15px;
  font-size: 18px;
  text-transform: uppercase;
  font-weight: 600;
  background-color: darkgreen;
  text-decoration: none;
  color: #f1f1f1;
  cursor: pointer;
}

.items::after {
  content: "";
  display: block;
  width: 0%;
  height: 1px;
  background-color: white;
  margin: 0 auto;
  transition: width 0.4s ease-in-out;
}

.items:hover::after {
  width: 100%;
}

.btn {
  position: absolute;
  top: 0;
  width: 70px;
  right: 10px;
  height: 40px;
  margin: 5px 0;
  display: none;
}

@media screen and (max-width: 500px) {
  nav{
    display: block;
  }
  .nav-list {
    display: flex;
    flex-direction: column;
    position: relative;
    top: 100%;
  }

  .items:nth-child(1) {
    border-top: 1px solid #fff;
  }

  .items {
    height: 70px;
    line-height: 70px;
    margin-right: 0;
    margin-left: 0;
    width: 100%;
    text-align: center;
    border-bottom: 2px solid #fff;
  }

  .items a {
    height: 100%;
    margin: 0;
    display: inline-block;
    width: 100%;
  }

  .btn{
    display: block;
  }
}
